@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-text>h1, .header-text>p {
    font-family: 'Roboto', sans-serif;
}

.header-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 72px;
	text-align: center;
}

.header-text h1, p {
	margin: 0;
}

.header-text h1 {
    padding: 0 120px;
	font-size: 34px;
}

.header-text p {
    padding: 0 186px;
	margin-top: 16px;
	font-size: 18px;
	font-weight: 500;
}

@media screen and (max-width: 999px) {

    .header-text {
        margin-top: 52px;
    }

}

@media screen and (min-width: 320px) {

	.header-text>h1, .header-text>p {
		padding: 0 24px;
	}

	.header-text h1 {
		font-size: 26px;
	}

	.header-text p {
		margin-top: 15px;
	}

}

@media screen and (min-width: 375px) {

	.header-text h1 {
		font-size: 30px;
	}

	.header-text p {
		margin-top: 12px;
	}

}

@media screen and (min-width: 414px) {

	.header-text>h1, .header-text>p {
		padding: 0 36px;
	}

	.header-text h1 {
		font-size: 34px;
	}

	.header-text p {
		margin-top: 12px;
	}

}

@media screen and (min-height: 812px) {

	.header-text h1 {
		font-size: 30px;
	}

	.header-text>h1, .header-text>p {
		padding: 0 24px;
	}

	.header-text p {
		margin-top: 12px;
	}

}

@media screen and (min-width: 1000px) {

    .header-text>h1 {
		padding: 0 120px;
    }
    
    .header-text p {
        padding: 0 186px;
		margin-top: 16px;
	}

}
.tarif-block {
	display: flex;
	flex-direction: column;
	align-items: center;
	border-radius: 10px;
	box-shadow: 0px 2px 24px rgba(0, 0, 0, 0.1);
	min-height: 444px;
	width: 270px;
	margin-bottom: 16px;
}

.price-block {
	text-align: center;
	margin: 44px 0 28px 0;
}

.cost  {
	display: flex;
	flex-direction: row;
}

.cost h2 {
	margin: 0;
	font-size: 48px;
	font-weight: 500;
}

.cost h1 {
	margin: 0;
	margin-top: 18px;
	font-size: 34px;
	font-weight: 700;
}

.cost>span {
	padding-top: 3%;
	font-size: 16px;
	font-weight: 500;
}


.price-block p {
	margin: 0;
	margin-top: 3px;
	font-size: 16px;
	font-weight: 500;
}

.btn {
	height: 36px;
	border-radius: 10px;
	background-color: #4BB34B;
	width: 85%;
	text-align: center;
	text-decoration: none;
	font-size: 17px;
	color: #FFFFFF;
	font-family: 'Roboto', sans-serif;
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.advantages {
	margin-top: 28px;
	margin-bottom: 32px;
}

.list {
	padding: 0;
	margin: 0;
}

.list>li {
	color: #2A2A2A;
	font-family: 'Roboto', sans-serif;
	list-style-type: none;
	margin-bottom: 8px;
	font-size: 14px;
}

@media screen and (min-width: 320px) {

	.tarif-block {
		min-height: 360px;
		width: 272px;
	}

}

@media screen and (min-width: 375px) {

	.tarif-block {
		min-height: 383px;
		width: 327px;
	}

}

@media screen and (min-width: 414px) {

	.tarif-block {
		min-height: 360px;
		width: 342px;
	}


}

@media screen and (min-height: 812px) {

	.tarif-block {
		min-height: 360px;
	}

}

@media screen and (min-width: 1000px) {

	.tarif-block {
		min-height: 444px;
		width: 270px;
	}

}
.tarif-container {
    padding: 0 80px;
    margin-top: 60px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
	justify-content: space-between;
}

@media screen and (max-width: 999px) {

	.tarif-container {
        padding: 0;
        flex-direction: column;
        align-items: center;
	}

}
