.tarif-container {
    padding: 0 80px;
    margin-top: 60px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
	justify-content: space-between;
}

@media screen and (max-width: 999px) {

	.tarif-container {
        padding: 0;
        flex-direction: column;
        align-items: center;
	}

}