@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

.header-text>h1, .header-text>p {
    font-family: 'Roboto', sans-serif;
}

.header-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 72px;
	text-align: center;
}

.header-text h1, p {
	margin: 0;
}

.header-text h1 {
    padding: 0 120px;
	font-size: 34px;
}

.header-text p {
    padding: 0 186px;
	margin-top: 16px;
	font-size: 18px;
	font-weight: 500;
}

@media screen and (max-width: 999px) {

    .header-text {
        margin-top: 52px;
    }

}

@media screen and (min-width: 320px) {

	.header-text>h1, .header-text>p {
		padding: 0 24px;
	}

	.header-text h1 {
		font-size: 26px;
	}

	.header-text p {
		margin-top: 15px;
	}

}

@media screen and (min-width: 375px) {

	.header-text h1 {
		font-size: 30px;
	}

	.header-text p {
		margin-top: 12px;
	}

}

@media screen and (min-width: 414px) {

	.header-text>h1, .header-text>p {
		padding: 0 36px;
	}

	.header-text h1 {
		font-size: 34px;
	}

	.header-text p {
		margin-top: 12px;
	}

}

@media screen and (min-height: 812px) {

	.header-text h1 {
		font-size: 30px;
	}

	.header-text>h1, .header-text>p {
		padding: 0 24px;
	}

	.header-text p {
		margin-top: 12px;
	}

}

@media screen and (min-width: 1000px) {

    .header-text>h1 {
		padding: 0 120px;
    }
    
    .header-text p {
        padding: 0 186px;
		margin-top: 16px;
	}

}